<!--
 * @Author: Demon3443002624@qq.com
 * @Date: 2022-4-6 11:03:05
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-18 16:11:45
 * @Description: 日运维图
 * @Description: 脱离全局机组选择
 * @Description: 
 * 1 0-1：功率因素 r_044
 * 2 0-30：电瓶电压 r_048
 * 3 0-100：水温、燃油液位 r_050,r_052
 * 4 0-400：三相电流 r_025,r_026,r_027
 * 5 0-600：油压、三相电压（UA、UB、UC、UAB、UAC、UBC） r_051,r_010,r_011,r_012,r_013,r_014,r_015
 * 0 0-2000：转速 r_047
 * 配置项在 data.yIndexMapping 中
-->
<template>
  <div>
    <BMainTopComponent />
    <div class="operational-chart-daily">
      <div class="info-container">
        <div>
          <div class="info-container-row">
            <my-cell title="单日运维图报告" :isHead="1" :isTitle="1"></my-cell>
            <div @click="handleShowBtn" class="press">{{ showFlag ? '收起' : '展开' }}</div>
          </div>
          <div v-show="showFlag" class="info-container-row">
            <my-cell title="项目信息" :isHead="1"></my-cell>
            <my-cell title="机组信息" :isHead="1"></my-cell>
            <my-cell title="配置信息" :isHead="1"></my-cell>
          </div>
          <div v-show="showFlag" class="info-container-row">
            <my-cell title="项目编号"> </my-cell>
            <my-cell title="" :value="baseInfo.GensetProject.ProjectId"> </my-cell>
            <my-cell title="项目名称"> </my-cell>
            <my-cell title="" :value="baseInfo.GensetProject.ProjectName"> </my-cell>
            <my-cell title="发动机型号"> </my-cell>
            <my-cell title="" :value="baseInfo.EngineModel"> </my-cell>
            <my-cell title="发电机型号"> </my-cell>
            <my-cell title="" :value="baseInfo.GeneratorModel"> </my-cell>
            <my-cell title="限制功率"></my-cell>
            <my-cell title="" :value="'●暂无'"></my-cell>
            <my-cell title="额定功率"></my-cell>
            <my-cell title="" :value="baseInfo.RatedPower"></my-cell>
          </div>
          <div v-show="showFlag" class="info-container-row">
            <my-cell title="项目类型"></my-cell>
            <my-cell title="" :value="baseInfo.GensetProject.ProjectType"></my-cell>
            <my-cell title="项目时间"></my-cell>
            <my-cell title="" :value="baseInfo.GensetProject.StartDate"></my-cell>
            <my-cell title="控制器型号"></my-cell>
            <my-cell title="" :value="baseInfo.CollectorModel"></my-cell>
            <my-cell :col="2"></my-cell>
            <my-cell title="额定转速"></my-cell>
            <my-cell title="" :value="baseInfo.RatedSpeed"></my-cell>
            <my-cell title="额定电流"></my-cell>
            <my-cell title="" :value="baseInfo.RatedCurrent"></my-cell>
          </div>
          <div v-show="showFlag" class="info-container-row">
            <my-cell title="项目地址"></my-cell>
            <my-cell title="" :value="baseInfo.GensetProject.ProjectAddress" :col="3"></my-cell>
            <my-cell :col="4"></my-cell>
            <my-cell title="额定频率"></my-cell>
            <my-cell title="" :value="baseInfo.RatedFrequency"></my-cell>
            <my-cell title="额定电压"></my-cell>
            <my-cell title="" :value="baseInfo.RatedVoltage"></my-cell>
          </div>
          <div v-show="showFlag" class="info-container-row">
            <my-cell title="启动次数"></my-cell>
            <my-cell title="" :value="baseInfo.BootTimes"></my-cell>
            <my-cell title="海拔"></my-cell>
            <my-cell title="" :value="baseInfo.Altitude"></my-cell>
            <my-cell title="环境温度"></my-cell>
            <my-cell title="" :value="baseInfo.Temperature"></my-cell>
            <my-cell :col="6"></my-cell>
            <!-- 20220829 周一 日运维图和周运维暂不显示字段：是否可用、启动状态、投入状态 -->
            <!-- <my-cell title="是否可用"></my-cell>
            <my-cell title="" :value="'●暂无'"></my-cell>
            <my-cell title="启动状态"></my-cell>
            <my-cell title="" :value="'●暂无'"></my-cell>
            <my-cell title="投入状态"></my-cell>
            <my-cell title="" :value="'●暂无'"></my-cell> -->
          </div>
        </div>
        <div v-show="showFlag" class="video-container">
          <video
            width="100%"
            height="100%"
            controls="controls"
            ref="myVideo"
            autoplay="autoplay"
            muted="muted"
          ></video>
        </div>
      </div>
      <el-row v-show="showFlag" class="alert-container">
        <div class="alert">
          <div>运行报警</div>
          <div>
            <el-popover
              v-for="(item, index) in this.alert.operating"
              :key="index"
              :title="item.title"
              placement="top-start"
              width="200"
              trigger="hover"
            >
              <p v-for="(e, i) in item.items" :key="i" :style="{ color: 'red' }">
                {{ e.time }}&nbsp;{{ e.content }}
              </p>
              <div>
                <el-button type="text" size="mini" @click="handleLinkToRR"
                  >点击跳转预警页面</el-button
                >
              </div>
              <div
                :style="{
                  left: item.per,
                  // [x] 避免两个盒子左右边框重叠
                  zIndex: 100 - index
                }"
                class="a-alert"
                slot="reference"
              ></div>
            </el-popover>
          </div>
        </div>
        <div class="alert">
          <div>维保报警</div>
          <div>
            <el-popover
              v-for="(item, index) in this.alert.maintenance"
              :key="index"
              :title="item.title"
              placement="top-start"
              width="200"
              trigger="hover"
            >
              <p v-for="(e, i) in item.items" :key="i" :style="{ color: 'red' }">
                {{ e.time }}&nbsp;{{ e.content }}
              </p>
              <div>
                <el-button type="text" size="mini" @click="handleLinkToRR"
                  >点击跳转预警页面</el-button
                >
              </div>
              <div
                :style="{
                  left: item.per
                }"
                class="a-alert"
                slot="reference"
              ></div>
            </el-popover>
          </div>
        </div>
        <div class="alert">
          <div>天气</div>
          <div>
            <el-popover
              v-for="(item, index) in this.alert.weather"
              :key="index"
              :title="item.title"
              placement="top-start"
              width="200"
              trigger="hover"
            >
              <p v-for="(e, i) in item.items" :key="i" :style="{ color: 'red' }">
                {{ e.time }}&nbsp;{{ e.content }}
              </p>
              <div>
                <el-button type="text" size="mini" @click="handleLinkToRR"
                  >点击跳转预警页面</el-button
                >
              </div>
              <div
                :style="{
                  left: item.per
                }"
                class="a-alert"
                slot="reference"
              ></div>
            </el-popover>
          </div>
        </div>
        <div class="alert">
          <div>维保周期</div>
          <div class="duration">
            <div>
              <div
                v-for="(item, index) in maintenanceDuration"
                :key="index"
                :style="{
                  left: item.per * 100 + '%',
                  borderLeft: Number(item.per) == 0 ? '' : '1px solid #7fb1df'
                }"
                duration
              >
                {{ item.name }}
              </div>
            </div>
            <div>
              <div tick v-for="(item, index) in 23" :key="index"></div>
            </div>
            <div>
              <div text v-for="(item, index) in 23" :key="index">
                <!-- {{item}}{{index}} -->
                {{ `${item > 9 ? item : '0' + item}:00` }}
              </div>
            </div>
          </div>
        </div>
      </el-row>
      <el-row class="toolbox-container" :gutter="10">
        <el-col :span="4"
          >项目：
          <el-select placeholder="请选择" v-model="query.project">
            <el-option
              v-for="item in opt.project"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="4"
          >机组：
          <el-select v-model="query.mch" no-data-text="该项目未绑定机组">
            <el-option
              v-for="item in opt.mchList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="4"
          >日期：<el-date-picker
            :clearable="false"
            v-model="query.date"
            type="date"
            placeholder="选择日期"
            placement="bottom-start"
          >
          </el-date-picker>
        </el-col>
        <el-col :span="12"
          >&nbsp;&nbsp;
          <el-popover v-model="visible" placement="top" width="350" trigger="manual">
            <div>
              <el-checkbox
                :indeterminate="isIndeterminate"
                v-model="checkAll"
                @change="handleCheckAllChange"
                >全选
              </el-checkbox>
              <el-button
                style="position: absolute; right: 45px; top: 1px"
                @click="visible = false"
                type="text"
                >关闭
              </el-button>
              <el-button
                style="position: absolute; right: 5px; top: 1px"
                type="text"
                @click="handleCurveParamChange"
                >确定
              </el-button>
              <div style="margin: 15px 0"></div>
              <el-row>
                <el-checkbox-group v-model="checkedOpts" @change="handleCheckedCitiesChange">
                  <el-col v-for="(item, index) in curveOpt" :key="index" :span="6">
                    <el-checkbox :label="item.value">
                      {{ item.label }}
                    </el-checkbox>
                  </el-col>
                </el-checkbox-group>
              </el-row>
            </div>
            <el-button slot="reference" @click="visible = true" type="primary" size="mini"
              >曲线参数选择</el-button
            >
          </el-popover>
          &nbsp;&nbsp;
          <el-popover placement="top" width="380" trigger="click">
            <el-row type="flex">
              <el-col :span="8" :style="{ display: 'flex', justifyContent: 'center' }"
                ><el-button type="primary" size="mini" @click="handleLinkTo(1)"
                  >报警曲线记录</el-button
                ></el-col
              >&nbsp;
              <el-col :span="8" :style="{ display: 'flex', justifyContent: 'center' }"
                ><el-button type="primary" size="mini" @click="handleLinkTo(2)"
                  >一键报修记录</el-button
                ></el-col
              >&nbsp;
              <el-col :span="8" :style="{ display: 'flex', justifyContent: 'center' }"
                ><el-button type="primary" size="mini" @click="handleLinkTo(3)"
                  >启动曲线记录</el-button
                ></el-col
              >
            </el-row>
            <el-button slot="reference" type="primary" size="mini" title="">瞬时曲线</el-button>
          </el-popover>
          &nbsp;&nbsp;
          <el-button
            type="primary"
            size="mini"
            @click="handleLinkToRA('/operation/genset-cloud-message')"
            title="点击跳转云消息推送"
            >云消息推送</el-button
          >
          <el-button
            type="primary"
            size="mini"
            @click="dialogVisibleRepair = true"
            title="点击一键报修"
            >一键报修</el-button
          >
          &nbsp;&nbsp;&nbsp;
          <el-popconfirm
            confirm-button-text="好的"
            cancel-button-text="不用了"
            confirm-button-type="primary"
            title="是否确定发起调参申请？"
            @confirm="handleLinkToRA('')"
          >
            <el-button slot="reference" type="primary" size="mini" title="点击发起调参申请"
              >调参申请</el-button
            ></el-popconfirm
          >
        </el-col>
        <el-dialog
          width="30%"
          title="服务申请"
          :center="true"
          :visible.sync="dialogVisibleRepair"
          :show-close="false"
          :close-on-click-modal="false"
          :close-on-press-escape="true"
        >
          <el-form label-position="left" ref="form" :rules="rules" :model="form" label-width="80px">
            <el-form-item prop="name" label="报修产品">
              <el-input v-model="form.name" placeholder="请输入产品"></el-input>
            </el-form-item>
            <el-form-item prop="no" label="出厂编号">
              <el-input v-model="form.no" placeholder="请输入出厂编号"></el-input>
            </el-form-item>
            <el-form-item prop="username" label="联系人">
              <el-input v-model="form.username" placeholder="请输入联系人"></el-input>
            </el-form-item>
            <el-form-item prop="phone" label="联系电话">
              <el-input v-model="form.phone" placeholder="请输入联系电话"></el-input>
            </el-form-item>
            <el-form-item prop="address" label="服务地址">
              <el-input v-model="form.address" placeholder="请输入服务地址"></el-input>
            </el-form-item>
            <el-form-item label="故障图片">
              <el-upload
                accept=".jpg, .jpeg, .png"
                action="#"
                list-type="picture-card"
                :auto-upload="false"
                :limit="picLimit"
                :on-exceed="handleExceed"
                :multiple="true"
                :on-change="onChangePic"
                ref="refUploadPics"
              >
                <i slot="default" class="el-icon-plus"></i>
                <div slot="file" slot-scope="{ file }">
                  <img class="el-upload-list__item-thumbnail" :src="file.url" alt="" />
                  <span class="el-upload-list__item-actions">
                    <span
                      class="el-upload-list__item-preview"
                      @click="handlePictureCardPreview(file)"
                    >
                      <i class="el-icon-zoom-in"></i>
                    </span>
                    <!-- <span
                        v-if="!disabled"
                        class="el-upload-list__item-delete"
                        @click="handleDownload(file)"
                      >
                        <i class="el-icon-download"></i>
                      </span> -->
                    <span class="el-upload-list__item-delete" @click="handleRemove(file)">
                      <i class="el-icon-delete"></i>
                    </span>
                  </span>
                </div>
              </el-upload>
            </el-form-item>
            <el-form-item prop="desc" label="故障描述">
              <el-input v-model="form.desc" type="textarea"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button @click="handleCloseRepair">取消</el-button>
              <el-button @click="handleRepairReset">重置</el-button>
              <el-button type="primary" @click="handleSubmitForm">提交</el-button>
            </el-form-item>
          </el-form>
          <!-- <span slot="footer" class="dialog-footer">
            <el-button @click="handleCloseRepair">取 消</el-button>
            <el-button type="primary" @click="handleCommitRepair">确 定</el-button>
          </span> -->
        </el-dialog>
        <el-dialog :modal-append-to-body="true" :visible.sync="dialogVisiblePic">
          <img width="100%" :src="dialogImageUrl" alt="" />
        </el-dialog>
        <!-- <el-col :span="3" :push="1"> </el-col> -->
      </el-row>
      <div v-loading="loading" element-loading-text="数据加载中..." class="chart-container">
        <my-chart
          :showFlag="showFlag"
          :type="type"
          :defaultChartInterval="defaultChartInterval"
          :seriesData="seriesData"
          :xAxisData="xAxisData"
        ></my-chart>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import Hls from 'hls.js'
import _ from 'lodash'
import dayjs from 'dayjs'
import { mapActions, mapGetters } from 'vuex'

import MyChart from './chart.vue'
import { formatTime } from '@/utils/common'
import { baseInfoDefault } from '../operationalChartWeekly/data'

Vue.component('MyCell', {
  props: ['value', 'title', 'isHead', 'col', 'isTitle'],
  data() {
    return {
      borderColor: '#7fb1df'
    }
  },
  template: `<div
  :style="{
      height:'35px',
      flex:col||1,
      }">
        <div v-if="!isHead"
        :style="{
            display:'flex',
            justifyContent:!title?'left':'center',
            alignItems:'center',
            height:'100%',
            borderBottom:'1px solid '+borderColor,
            borderRight:'1px solid '+borderColor,
        }"
        ><div v-if="!title">&nbsp;</div>{{title}}{{value}}</div>
        <div v-if="!!isHead"
        :style="{
            display:'flex',
            justifyContent:'center',
            alignItems:'center',
            height:'100%',
            fontSize:isTitle?'2rem' : '1.2rem',
            fontWeight:'bold',
            borderBottom:'1px solid '+borderColor,
            borderRight:'1px solid '+borderColor,
        }"
        >{{title}}</div>
      </div>`
})
export default {
  name: 'OperationalChartDaily',
  components: {
    MyChart
  },
  data() {
    return {
      form: {
        name: '',
        no: '',
        username: '',
        phone: '',
        address: '',
        desc: ''
      },
      // 默认显示最后一个数据点前2个小时数据
      defaultChartInterval: [],
      picLimit: 3,
      showFlag: true,
      rules: {
        name: [{ required: true, message: '请输入产品名称', trigger: 'blur' }],
        no: [{ required: true, message: '请输入产品编号', trigger: 'blur' }],
        username: [{ required: true, message: '请输入联系人', trigger: 'blur' }],
        phone: [{ required: true, message: '请输入联系电话', trigger: 'blur' }],
        address: [{ required: true, message: '请输入服务地址', trigger: 'blur' }]
      },
      dialogImageUrl: '',
      dialogVisiblePic: false,
      // 机组项目基础信息
      baseInfo: baseInfoDefault,
      // 项目类型
      projectTypeMapping: ['', '运维', '代运维'],
      // 下拉框选项
      opt: {
        project: [],
        mchList: []
      },
      // 工具栏查询条件
      query: {
        project: '',
        mch: '',
        date: dayjs().format('YYYY-MM-DD')
      },
      // 萤石云视频地址
      VideoH5Url: null,
      // 报警信息
      alert: {
        operating: [],
        maintenance: [],
        weather: []
      },
      // 维保周期
      maintenanceDuration: [
        {
          name: '1-1-17',
          startTime: '2022-3-18 09:00:00',
          endTime: '2022-3-18 17:00:00'
        }
      ],
      // 曲线数据筛选
      checkAll: false,
      checkedOpts: [],
      defaultChecked: ['r_047'],
      curveOpt: [],
      isIndeterminate: true,
      visible: false,
      hls: null,
      // 曲线数据
      seriesData: [],
      xAxisData: [],
      // 曲线的类型，时间轴、类目轴
      // type:'time', x轴为时间轴时tooltip仅能显示一个系列
      // type: 'category'
      type: 'time',
      loading: false,
      yIndexMapping: [
        'r_047',
        'r_044',
        'r_048',
        'r_050,r_052',
        'r_025,r_026,r_027',
        'r_051,r_010,r_011,r_012,r_013,r_014,r_015'
      ],
      // 初次加载标志
      onceFlag: true,
      dialogVisibleRepair: false
    }
  },
  computed: {
    ...mapGetters(['selectCrew', 'currentProject'])
  },
  watch: {
    // 1.更新 vuex 所选设备；2.刷新页面数据（基础信息+曲线数据）
    'query.mch': {
      handler: function (nv, ov) {
        // 项目机组列表仅包含机组编号和名字，需查询完整机组信息
        this.$api.screenData.getsingleMchInfo({ id: nv }).then((value) => {
          const data = value.Data
          data && this.toggleSelectCrew(data)
        })
        this.getData()
      }
    },
    // 刷新曲线数据（机组存在）
    'query.date': {
      handler: function (nv, ov) {
        this.getData()
      }
    },
    // 根据所选项目筛选机组（不选表示全部机组）
    'query.project': {
      handler: function (nv, ov) {
        // 切换项目
        this.$api.project.GetGensetProject({ id: nv }).then((value) => {
          const data = value.Data
          if (data) this.toggleCurrentProject(data)
        })
        this.getMchList()
      }
    }
  },
  methods: {
    // vuex
    ...mapActions(['toggleSelectCrew', 'toggleCurrentProject']),
    // 获取视频地址
    getVideoUrl() {
      // 使用 hls 实现
      this.$api.screenData.FindGeneratorSetByMonitor({ id: this.query.mch }).then((resOriginal) => {
        const res = resOriginal.Data
        this.VideoH5Url = res && res.VideoH5Url ? res.VideoH5Url : ''
        if (!!this.VideoH5Url) {
          const hls = new Hls()
          hls.attachMedia(this.$refs.myVideo)
          hls.on(Hls.Events.MEDIA_ATTACHED, (_) => {
            hls.loadSource(this.VideoH5Url)
            hls.on(Hls.Events.MANIFEST_PARSED, function () {
              this.$refs.myVideo.play()
            })
          })
          // 出错处理
          hls.on(Hls.Events.ERROR, function (event, data) {
            if (data.fatal) {
              switch (data.type) {
                case Hls.ErrorTypes.NETWORK_ERROR:
                  // try to recover network error
                  console.error('fatal network error encountered, try to recover')
                  hls.startLoad()
                  break
                case Hls.ErrorTypes.MEDIA_ERROR:
                  console.error('fatal media error encountered, try to recover')
                  hls.recoverMediaError()
                  break
                default:
                  // cannot recover
                  hls.destroy()
                  break
              }
            }
          })
          this.hls = hls
        } else {
          !!this.hls && this.hls.destroy()
        }
      })
    },
    // 获取机组项目基础信息
    getBaseInfo() {
      const generatorSetId = this.query.mch
      const query = {
        GeneratorSetId: generatorSetId
      }
      this.$api.operation.getGensetProjectGensetDetail({}, query).then((res) => {
        const data = !!res.Data && res.Data
        if (data) {
          this.baseInfo = { ...data }
          try {
            // 格式化时间
            this.baseInfo.GensetProject.StartDate = formatTime(
              this.baseInfo.GensetProject.StartDate
            ).format('YYYY-MM-DD')
            this.baseInfo.GensetProject.EndDate = formatTime(
              this.baseInfo.GensetProject.EndDate
            ).format('YYYY-MM-DD')
            // TODO 暂无 项目类型
            this.baseInfo.GensetProject.ProjectType =
              this.projectTypeMapping[this.baseInfo.GensetProject.ProjectType]
          } catch (err) {}
        }
      })
      // 获取报警记录
      const paramsAlert = {
        TimeStart: dayjs(this.query.date).format('YYYY-MM-DD 00:00:00'),
        TimeEnd: dayjs(this.query.date).add(1, 'd').format('YYYY-MM-DD 00:00:00'),
        Mode: '-1'
      }
      const queryAlert = {
        id: this.query.mch
      }
      this.$api.operation.findGeneratorSetHistoryAlarm(paramsAlert, queryAlert).then((res) => {
        const alarms = !!res.Data && !!res.Data.Alarms && res.Data.Alarms
        if (alarms) {
          const operating = new Array()
          alarms.forEach((item) => {
            const obj = {
              time: formatTime(item.StartTime).format('YYYY-MM-DD HH:00:00'),
              timeTrue: formatTime(item.StartTime).format('HH:mm'),
              title: item.TypeName,
              content: item.Describe
            }
            operating.push(obj)
          })
          // 2022-5-12 10:55:04 同一个小时内的报警在一个报警框中显示
          const newOperating = new Object()
          operating.forEach((e) => {
            const key = dayjs(e.time).get('hour')
            if (newOperating[key]) {
              newOperating[key].items.push({
                time: e.timeTrue,
                content: e.content
              })
            } else {
              newOperating[key] = {
                items: [
                  {
                    time: e.timeTrue,
                    content: e.content
                  }
                ],
                title: e.title,
                time: e.time
              }
            }
          })
          this.alert.operating = newOperating
          this.handleTime()
        }
      })
      // 获取维保记录
      const paramsMaintenance = {
        TimeStart: dayjs(this.query.date).format('YYYY-MM-DD 00:00:00'),
        TimeEnd: dayjs(this.query.date).add(1, 'd').format('YYYY-MM-DD 00:00:00')
      }
      const queryMaintenance = {
        GeneratorSetId: this.query.mch
      }
      this.$api.operation
        .getGensetProjectGensetMaintenanceRemind(paramsMaintenance, queryMaintenance)
        .then((res) => {
          const alarms = !!res.Data && res.Data
          alarms.map((e) => {
            e.NextRemindDate = e.StartTime
            e.ItemContent = e.Describe
          })
          if (alarms) {
            const maintenance = new Array()
            alarms.forEach((item) => {
              const obj = {
                time: formatTime(item.NextRemindDate).format('YYYY-MM-DD HH:00:00'),
                timeTrue: formatTime(item.NextRemindDate).format('HH:mm'),
                title: '维保提醒',
                content: item.ItemContent
              }
              maintenance.push(obj)
            })
            const newMaintenance = new Object()
            maintenance.forEach((e) => {
              const key = dayjs(e.time).get('hour')
              if (newMaintenance[key]) {
                newMaintenance[key].items.push({
                  time: e.timeTrue,
                  content: e.content
                })
              } else {
                newMaintenance[key] = {
                  items: [
                    {
                      time: e.timeTrue,
                      content: e.content
                    }
                  ],
                  title: e.title,
                  time: e.time
                }
              }
            })
            this.alert.maintenance = newMaintenance
            this.handleTime()
          }
        })
      // 获取天气记录
      const paramsWeather = {
        TimeStart: dayjs(this.query.date).format('YYYY-MM-DD 00:00:00'),
        TimeEnd: dayjs(this.query.date).add(1, 'd').format('YYYY-MM-DD 00:00:00')
      }
      const queryWeather = {
        GeneratorSetId: this.query.mch
      }
      this.$api.operation.getGensetWeather(paramsWeather, queryWeather).then((res) => {
        const alarms = !!res.Data && res.Data
        alarms.map((e) => {
          e.NextRemindDate = e.StartTime
          e.ItemContent = e.Describe
        })
        if (alarms) {
          const weather = new Array()
          alarms.forEach((item) => {
            const obj = {
              // time: formatTime(item.SendTime).format('YYYY-MM-DD HH:00:00'),
              // timeTrue: formatTime(item.SendTime).format('HH:mm'),
              time: item.SendTime,
              timeTrue: item.SendTime,
              title: '天气预警',
              content: item.Headline
            }
            weather.push(obj)
          })
          const newWeather = new Object()
          weather.forEach((e) => {
            const key = dayjs(e.time).get('hour')
            if (newWeather[key]) {
              newWeather[key].items.push({
                time: e.timeTrue,
                content: e.content
              })
            } else {
              newWeather[key] = {
                items: [
                  {
                    time: e.timeTrue,
                    content: e.content
                  }
                ],
                title: e.title,
                time: e.time
              }
            }
          })
          this.alert.weather = newWeather
          this.handleTime()
        }
      })
      // 获取视频监控
      this.getVideoUrl()
      // TODO 暂无 获取维保周期
    },
    // 获取项目列表，首次加载获取，默认选中第一个项目
    getProjectList() {
      this.$api.operation.findGensetProjectIndexProject().then((res) => {
        const data = !!res.Data && res.Data
        if (data && data.length && data.length > 0) {
          const projectList = new Array()
          data.forEach((item) => {
            const obj = {
              value: item.ProjectId,
              label: item.ProjectName
            }
            projectList.push(obj)
          })
          this.opt.project = projectList
          this.$nextTick(function () {
            this.getMchListFirst()
          })
          /*
            // 因为缓存，所选机组必定存在，这一步省略
            // 存在机组&&（项目）
            // 不存在机组&&项目
            // 不存在机组&&无项目
            // 有机组
            const crewFlag = !!this.selectCrew.ID
            // 有项目
            const projectFlag = !!this.currentProject.projectFlag
            if (crewFlag) {
              // 根据机组选择项目
            } else if (!crewFlag && projectFlag) {
              // 选中该项目
              projectList.forEach((e) => {
                if (e.value.toString() === this.currentProject.ProjectId)
                  this.query.project = e.value
              })
            } else {
              this.$nextTick(function () {
                this.query.project = projectList[0].value
              })
            }
            */
        }
      })
    },
    // 获取机组列表（根据所选项目）
    getMchList() {
      const params = { ProjectId: this.query.project }
      this.$api.operation.findGensetProjectIndexGenset(params).then((res) => {
        const data = !!res.Data && res.Data
        if (data && data.length && data.length > 0) {
          const mchList = new Array()
          data.forEach((item) => {
            const obj = {
              value: item.GeneratorSetId,
              label: item.DisplayName
            }
            mchList.push(obj)
          })
          this.opt.mchList = mchList
          this.$nextTick(function () {
            if (!this.onceFlag) this.query.mch = mchList[0].value
            else {
              // 初次加载标志反转
              this.onceFlag = false
              const query = this.$route.query
              let crewId = undefined
              if (query) crewId = query.crewId
              if (!!crewId) {
                this.query.mch = Number(crewId)
              } else {
                this.query.mch = this.selectCrew.ID
              }
            }
          })
        } else {
          this.$message('该项目未绑定机组！')
          this.opt.mchList = []
          this.query.mch = ''
        }
      })
    },
    // 获取机组列表（初次获取信息）

    getMchListFirst() {
      let crewId = this.selectCrew && !!this.selectCrew.ID && this.selectCrew.ID
      // 打开新页面,则使用传过来的参数
      const query = this.$route.query
      if (query) {
        const type = query.type
        const crewID = query.crewId
        if (type === 'newPage') {
          crewId = crewID
        }
      }

      // 根据机组获取项目
      !!crewId &&
        this.$api.operation
          .getGensetProjectGensetDetail({}, { GeneratorSetId: crewId })
          .then((value) => {
            const data = value.Data
            if (data) {
              const projectId = data.GensetProject && data.GensetProject.ProjectId
              if (projectId) {
                this.opt.project.forEach((e) => {
                  if (e.value.toString() === projectId.toString()) {
                    this.query.project = e.value
                  }
                })
              } else {
                this.query.project = this.opt.project[0].value
              }
            }
          })
    },
    // 获取曲线参数
    getCurveOpt() {
      const arr = [
        {
          value: 'r_047',
          label: '转速'
        },
        {
          value: 'r_051',
          label: '油压'
        },
        {
          value: 'r_044',
          label: '功率因素'
        },
        {
          value: 'r_050',
          label: '水温'
        },
        {
          value: 'r_052',
          label: '燃油液位'
        },
        {
          value: 'r_048',
          label: '电瓶电压'
        },
        {
          value: 'r_010,r_011,r_012,r_013,r_014,r_015',
          label: '三相电压'
        },
        {
          value: 'r_025,r_026,r_027',
          label: '三相电流'
        }
      ]
      this.curveOpt = arr
    },
    // 获取曲线数据
    getChartData() {
      // 根据所选机组和所选参数获取曲线数据
      let items = []
      this.checkedOpts.forEach((item) => {
        items = [...items, ...item.split(',')]
      })
      const params = {
        // TimeStart: dayjs(this.query.date).add(-5, 'd').format('YYYY-MM-DD 00:00:00'),
        TimeStart: dayjs(this.query.date).format('YYYY-MM-DD 00:00:00'),
        TimeEnd: dayjs().format('YYYY-MM-DD HH:mm:ss'),
        Items: items.join(',')
      }
      // 如果非当天 时间取所选日期的00:00:00到第二天00:00:00
      if (dayjs().format('YYYY-MM-DD') != dayjs(this.query.date).format('YYYY-MM-DD')) {
        const date = this.query.date
        params.TimeStart = dayjs(date).hour(0).minute(0).second(0)
        params.TimeEnd = dayjs(date).hour(24).minute(0).second(0)
      }
      const query = {
        GeneratorSetId: this.query.mch
        // GeneratorSetId: 7958
      }
      const requestBegin = +new Date()
      // const loading = this.$loading({
      //   lock: true,
      //   text: 'Loading...',
      //   spinner: 'el-icon-loading',
      //   background: 'rgba(0, 0, 0, 0.7)'
      // })
      this.loading = true
      this.$api.operation.findGeneratorSetHistoryDataV4(params, query).then((res) => {
        const requestInterval = (+new Date() - requestBegin) / 1000 + 's'
        console.info(
          `%c${requestInterval}`,
          'color:red;background:yellow;font-weight:bold',
          '(request)'
        )
        const handleDataBegin = +new Date()
        let circulation = 0
        const data = !!res.Data && res.Data
        // 处理后的数据
        const useData = {}
        data.Datas.forEach((item) => {
          useData[item.No] = {
            describe: item.Describe,
            data: item.Data
          }
          const data = item.Data
          useData[item.No].dataMap = new Map()
          data.forEach((e) => {
            useData[item.No].dataMap.set(e.Time.split('/Date(')[1].split(')/')[0], e.Value)
          })
        })
        const time = {
          begin: +dayjs(params.TimeStart),
          end: +dayjs(params.TimeEnd),
          // interval秒一个点
          interval: 1
        }
        // 获取模拟数据
        function getRandom(begin, end) {
          return Number((Math.random() * (end - begin) + begin).toFixed(0))
        }
        const seriesData = []
        const xAxisData = []
        // 数据稳定时间间隔（5分钟）
        const timeInterval = 5 * 60
        // x轴数据只计一次
        let index = 0
        for (const ky in useData) {
          const seriesName = useData[ky].describe
          const dataMap = useData[ky].dataMap
          const obj = {
            name: seriesName,
            yAxisIndex: this.getYAxisIndex(ky),
            data: []
          }
          // 本系列时间累计器
          let accumulation = 0
          let lastValue = '-'

          for (let i = time.begin; i < time.end; i += time.interval * 1000) {
            circulation++
            // 滑动到此刻的情况
            // 1.此刻无值，前 timeInterval 时间内有值，补为 timeInterval 时间内的值,累加器加 interval
            // 2.此刻无值，前 timeInterval 时间内无值，补为'-'
            // 3.此刻有值，计时器清零
            const nowFormat = i
            const value = dataMap.get(nowFormat.toString())
            let thisValue = ''
            if (value === undefined) {
              if (accumulation < timeInterval) {
                thisValue = lastValue
                accumulation += time.interval
              } else {
                thisValue = '-'
              }
            } else if (value !== undefined) {
              thisValue = value
              lastValue = value
              accumulation = 0
            }
            if (this.type === 'time') {
              obj.data.push([i, thisValue])
            } else {
              // obj.data.push(getRandom(begin, end))
              obj.data.push([nowFormat, thisValue])
              index !== 0 || xAxisData.push(nowFormat)
            }
          }
          seriesData.push(obj)
          index++
        }
        this.seriesData = seriesData
        this.xAxisData = xAxisData
        const defaultChartInterval = [time.begin, time.end]
        const newSeriesData = _.cloneDeep(seriesData)
        if (newSeriesData.length > 0) {
          newSeriesData[0].data.forEach((e, i) => {
            if (i === 0 && e[1] !== '-') defaultChartInterval[1] = e[0]
            else {
              if (e[1] !== '-') defaultChartInterval[1] = newSeriesData[0].data[i - 1][0]
            }
          })
          defaultChartInterval[0] = +dayjs(defaultChartInterval[1]).add(-2, 'h')
          defaultChartInterval.push(dayjs(defaultChartInterval[0]).format('yyyy-MM-DD HH:mm:ss'))
          defaultChartInterval.push(dayjs(defaultChartInterval[1]).format('yyyy-MM-DD HH:mm:ss'))
        }

        this.loading = false
        this.defaultChartInterval = defaultChartInterval
        const handleDataInterval = (+new Date() - handleDataBegin) / 1000 + 's'
        console.info(
          `%c${handleDataInterval}`,
          'color:red;background:yellow;font-weight:bold',
          '(handle data)'
        )
        console.info(
          `circulation %c${circulation}`,
          'color:red;background:yellow;font-weight:bold',
          'times'
        )
      })
    },
    // 获取数据
    getData() {
      if (!!this.query.mch) {
        this.getBaseInfo()
        this.getChartData()
      } else {
        this.$message('请选择机组！')
      }
    },
    // 获取 yAxisIndex
    getYAxisIndex(curveOpt) {
      let index = 0
      this.yIndexMapping.forEach((e, i) => {
        e.includes(curveOpt) && (index = i)
      })
      return index
    },
    handleShowBtn() {
      const f = this.showFlag
      const msg = f ? '收起' : '展开'
      this.$message.success(`项目信息已${msg}!`)
      this.showFlag = !f
    },
    // 跳转页面
    handleLinkTo(type) {
      let routePath = '/equipment/data-record/alarm-curve-record'
      switch (type) {
        case 2:
          routePath = '/equipment/data-record/oneclick-repairrecord'
          break
        case 3:
          routePath = '/equipment/data-record/starting-curve-record'
          break
        default:
          break
      }
      this.$router.push(routePath)
    },
    // 跳转远程调参页面
    handleLinkToRA(p) {
      const sc = this.selectCrew
      const params = {
        GeneratorSetId: sc.ID,
        GeneratorName: sc.DisplayName,
        GeneratorCollectorToken: sc.CollectorToken,
        GeneratorSerial: sc.Serial,
        ClientCompany: '',
        ClientName: '',
        ClientPhone: '',
        AuthCode: ''
      }
      p
        ? this.$router.push(p)
        : this.$api.operation.addGenSetServiceSheet(params).then((value) => {
            this.$message('调参申请成功！')
            this.$router.push('/operation/remoteTuning')
          })
    },
    // 跳转到报警记录页面
    handleLinkToRR() {
      this.$router.push('/equipment/data-record/alarm-curve-record')
    },
    // *** 曲线筛选条件相关
    handleCheckAllChange(val) {
      const curveOptValues = []
      this.curveOpt.forEach((item) => {
        curveOptValues.push(item.value)
      })
      this.checkedOpts = val ? curveOptValues : []
      this.isIndeterminate = false
    },
    handleCheckedCitiesChange(value) {
      let checkedCount = value.length
      this.checkAll = checkedCount === this.curveOpt.length
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.curveOpt.length
    },

    // 更新曲线图
    handleCurveParamChange() {
      if (!!this.query.mch) {
        this.visible = false
        this.getChartData()
      } else {
        this.$message('请选择机组！')
      }
    },
    // 计算报警和周期在时间轴上的位置
    handleTime() {
      const oneDaySecond = 24 * 60 * 60
      // 计算各个报警时间在一天中的那个位置
      for (const ky in this.alert) {
        const item = this.alert[ky]
        for (const ky2 in item) {
          const aAlert = item[ky2]
          const timeObj = dayjs(aAlert.time)
          const minute = timeObj.hour() * 60 * 60 + timeObj.minute() * 60 + timeObj.second()
          const per = ((minute * 100) / oneDaySecond).toFixed(2) + '%'
          const timeStr = timeObj.format('HH:mm')
          aAlert.minute = minute
          aAlert.per = per
          aAlert.timeStr = timeStr
        }
        // item.forEach((element) => {
        //   const aAlert = element
        //   const timeObj = dayjs(aAlert.time)
        //   const minute = timeObj.hour() * 60 * 60 + timeObj.minute() * 60 + timeObj.second()
        //   const per = ((minute * 100) / oneDaySecond).toFixed(2) + '%'
        //   const timeStr = timeObj.format('HH:mm')
        //   aAlert.minute = minute
        //   aAlert.per = per
        //   aAlert.timeStr = timeStr
        // })
      }
      // 计算维保周期在一天中的那个位置
      this.maintenanceDuration.forEach((item) => {
        const startTime = item.startTime
        const oneDaySecond = 24 * 60 * 60
        const timeObj = dayjs(startTime)
        const minute = timeObj.hour() * 60 * 60 + timeObj.minute() * 60 + timeObj.second()
        const per = minute / oneDaySecond
        item.per = per
      })
    },
    // 报修页面关闭事件
    handleCloseRepair() {
      this.handleRepairReset()
      this.dialogVisibleRepair = false
    },
    // 报修页面提交事件
    handleCommitRepair() {
      this.dialogVisibleRepair = false
    },
    handleRepairReset() {
      this.$message('重置成功！')
      this.$refs.form.resetFields()
      this.$refs.refUploadPics.clearFiles()
    },
    handleSubmitForm() {
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.$message('提交成功，请查看控制台信息')
          console.info(this.form, this.$refs.refUploadPics)
        } else {
          return false
        }
      })
    },
    // 图片上传相关
    handleRemove(file) {
      // upload 对象
      const uid = file.uid
      const upload = this.$refs.refUploadPics
      const uploadFiles = upload.uploadFiles
      try {
        uploadFiles.forEach((e, i) => {
          if (e.uid === uid) uploadFiles.splice(i, 1)
        })
      } catch (error) {
        this.$message({
          type: 'error',
          message: '删除失败！'
        })
        console.error(error)
      }
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url
      this.dialogVisiblePic = true
    },
    handleExceed() {
      this.$message(`仅限上传${this.picLimit}张图片！`)
    },
    onChangePic(file, fileList) {
      const isLt5M = file.size / 1024 <= 5120
      if (!isLt5M) {
        fileList.splice(fileList.indexOf(file), 1)
        this.$message.warning('上传文件大小不能超过 5MB!')
        return false
      }
    }
  },
  mounted() {
    this.checkedOpts = this.defaultChecked
    this.getCurveOpt()
    this.getProjectList()
    this.handleTime()
    // setInterval(() => {
    //   this.getData()
    // }, 60000)
  },
  destroyed() {
    // 销毁hls实例
    this.hls && this.hls.destroy()
  }
}
</script>

<style lang="less" scoped>
@borderColor: #7fb1df;

.operational-chart-daily {
  // [x] 修复qq浏览器flex布局问题
  // display: block !important;
  height: 100%;

  /deep/ .el-loading-mask {
    background-color: rgba(17, 36, 70, 0.6);
  }
  .info-container {
    display: flex;

    & > div:first-child {
      flex: 3;
      border: 1px solid @borderColor;
      border-right: 0;
      border-bottom: 0;
    }
    .table-title > div {
      font-size: 1.3rem !important;
    }
    .video-container {
      flex: 1;
      height: 210px;
      border-left: 0;
      border: 1px solid @borderColor;
      border-left: 0;
      border-bottom: 0;
    }

    &-row {
      display: flex;
      position: relative;
      .press {
        top: 8px;
        left: 8px;
        cursor: pointer;
        font-weight: 600;
        color: #209a86;
        font-size: 1.1rem;
        position: absolute;
        &:hover {
          color: rgb(17, 221, 187);
        }
      }
    }
  }

  .alert-container {
    border-top: 0;
    border: 1px solid @borderColor;

    .alert {
      display: flex;

      & > div {
        height: 35px;
        border-right: 1px solid @borderColor;
        border-bottom: 1px solid @borderColor;

        &:first-child {
          flex: 1;
          display: flex;
          align-items: center;
          justify-content: center;
        }

        &:last-child {
          flex: 15;
          border-right: 0;
          position: relative;

          .a-alert {
            width: 4.17%;
            height: 100%;
            cursor: pointer;
            // 横向居中显示
            // margin-left: -5px;
            background: red;
            position: absolute;
            border-right: 1px solid #8ab8e3;
            border-left: 1px solid #8ab8e3;
            // animation: flash .9s ease-in-out infinite;
            // animation: name duration timing-function delay iteration-count direction fill-mode;
          }

          @keyframes flash {
            0% {
              opacity: 0.2;
            }

            50% {
              opacity: 0.6;
            }

            100% {
              opacity: 0.99;
            }
          }

          &[class~='duration'] {
            > div {
              width: 100%;
              display: flex;

              > div[duration] {
                height: 35px;
                width: 33.35%;
                display: flex;
                position: absolute;
                align-items: center;
                background: #7fb1df;
                box-sizing: border-box;
                justify-content: center;
                border-right: 1px solid #7fb1df;
              }

              > div[tick] {
                height: 6px;
                margin: 35px 0 0 0;
                box-sizing: border-box;
                width: percentage(0.0417);
                border-right: 1px solid #7fb1df;
              }

              > div[text] {
                &:first-child {
                  margin: 0 0 0 2.08%;
                }

                height: 6px;
                display: flex;
                box-sizing: border-box;
                justify-content: center;
                width: percentage(0.0417);
                // margin-left: percentage(0.0117);
              }
            }
          }
        }
      }

      &:last-child > div {
        border-bottom: 0;
      }
    }
  }

  .toolbox-container {
    display: flex;
    padding: 5px 0;
    margin-top: 30px;

    .el-col {
      display: flex;
      white-space: nowrap;
      align-items: center;
    }
  }

  .chart-container {
    height: 450px;
  }
}
</style>



